
.media-type{
// Icon sizes
  background-color: #fff;
  padding: 8px $gel-spacing-unit $gel-alt-spacing-unit / 2;
  svg {
    height: 14px;
    width: 14px;
  }
 .media-type__duration{
  padding-left: $gel-spacing-unit / 2;
    vertical-align: text-bottom;
 }
.icon--xsmall &{
   svg {
    height: 12px;
    width:  12px;
   }
  }
  &.icon--small {
    background-color: red;
    padding: $gel-spacing-unit;
   svg {
    height: 16px;
    width: 16px;
   }
  }
  .icon--medium &{
    padding: $gel-alt-spacing-unit $gel-spacing-unit * 2 $gel-spacing-unit / 2;

    .media-type__duration{
      display: block;
      padding-left:0;
      text-align: center;
    }
   svg {
    padding: 4px;
    height: 40px;
    width: 40px;
   }
  }

  .sicon--small &{
    padding: 8px $gel-spacing-unit $gel-spacing-unit / 2;

    .media-type__duration{
      display: inline-block;
      padding-left:4px;
      text-align: center;
      vertical-align: bottom;
    }
   svg {

    height: 16px;
    width: 16px;

   }
  }

  .icon--large &{
    padding: 9px $gel-spacing-unit * 2 $gel-spacing-unit / 2;

    .media-type__duration{
      display: block;
      padding-left:0;
      text-align: center;
    }
   svg {
    padding: 6px;
    height: 36px;
    width: 36px;

   }
  }

  .icon--xlarge &{
    padding: $gel-alt-spacing-unit $gel-spacing-unit * 2 $gel-spacing-unit / 2;

    .media-type__duration{
      display: block;
      padding-left:0;
      text-align: center;
    }
   svg {
    padding: 8px;
    height: 64px;
    width: 64px;

   }
  }
}
