///*------------------------------------*\
//    # hydrogen USED FOR TOP STORIES
//\*------------------------------------*/
.hydrogen--features + .hydrogen--wrap {
  margin-top: 16px;
}
.hydrogen--wrap{
  @include mq($from:gel-bp-m) {
    margin-top: 16px;
  }

}
.hydrogen__item{
  @include mq($from: 'gel-bp-xs') {
    margin-bottom: $gel-alt-spacing-unit;
  }
  @include mq($from: 'gel-bp-m') {
    margin-bottom: $gel-spacing-unit * 2;
  }
  @include mq($from: 'gel-bp-xxl') {
    margin-bottom: $gel-spacing-unit * 2;
    &:nth-child(-n+2){
      margin-bottom: $gel-alt-spacing-unit * 2;
    }

  }
  position: relative;

  .promo--lead{
    @include mq($from:gel-bp-xs, $until:320px) {
      .promo__media {
        margin-right: -8px;
        margin-left: -8px;
      }
    }

   @include mq($from:320px, $until:gel-bp-m) {
     .promo__media {
       margin-right: -16px;
       margin-left: -16px;
     }
   }
  }

}



///*------------------------------------*\
//    # RIVER DISPLAYS HEADLINES ONLY
//\*------------------------------------*/

.hydrogen__river{
    .hydrogen__item--river{
      padding-bottom: $gel-spacing-unit * 2;
      min-height: 86px;
     }
  @include mq($from: 'gel-bp-xs', $until: 'gel-bp-m') {
    .hydrogen__item--river{
      padding-bottom: $gel-alt-spacing-unit;
    }
    .promo__media{
      display: block !important;
    }
  }
  @include mq($from: 'gel-bp-m', $until: 'gel-bp-l') {
    .hydrogen__item--river:nth-child(-n+6){
      padding-bottom: $gel-alt-spacing-unit * 2;
    }
   }
  @include mq($from: 'gel-bp-l', $until: 'gel-bp-xxl') {
    .hydrogen__item--river:nth-child(-n+4){
      padding-bottom: $gel-alt-spacing-unit * 2;
    }
  }
  @include mq($from: 'gel-bp-xxl') {
     padding-bottom: 16px;
     &:last-child{
       padding-bottom: 0;
     }
    .hydrogen__item--river{
        padding-top: $gel-alt-spacing-unit;
        padding-bottom: $gel-alt-spacing-unit;
      &:first-child{
        padding-top: 0;
      }
      &:last-child{
        padding-bottom: $gel-spacing-unit * 2;
      }
    }

  }

  .promo__media{
    display: none;
  }
  .promo__headline{
    @include gel-typography('english-bold');
  }
}


// if Hidrogen has more than 7 items
.hydrogen__item:nth-last-child(n+7), .hydrogen__item:nth-last-child(n+7) ~ .hydrogen__item:first-child {

  @include mq($from:gel-bp-l) {
    width: gel-columns(6/12) !important;
  }

.promo--lead {
  display: flex;
 justify-content: space-between;
  &.promo--has-image-4by3 .gel-responsive-image--16by9{
        padding-bottom: 65%;
  }
  .metadata{
    position: initial;
  }

 .label {
    display: inline-table;
  }
  .promo__body{
    padding-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: gel-columns(12/1);
    float: initial;
    padding-right: 0 !important;
  }
  .promo__media {
      width: initial;
      float: initial;
      padding-left: 0 !important;
      order: 0;
    }

  .gel-responsive-image{
    display: none;
  }
  .promo__headline{
   @include gel-typography('trafalgar-bold');
   }
  .promo__summary{
   @include gel-typography('english');
  }


}

}

///*------------------------------------*\
//    # HYDROGEN FEATURES
//\*------------------------------------*/


.hydrogen--features{
  background: #F4F4F4;
  padding-top: 16px;
  .media-type{
    background: #F4F4F4;
  }
}
.hydrogen--features + .hydrogen--features{
  padding-top: 0;
}
