.has-contributor{
  .promo__body{
    padding-bottom: 0;
  }
}
.promo__headline, .promo__summary {
  & + .contributor {
    margin-top: $gel-spacing-unit * 2;

  }
}

.contributor + .metadata{
  display: none;
}

.contributor {
  display: table;
  width: 100%;
}
  .contributor__avatar {
    padding-right: 12px;

    > img {
      display: block;
      max-width: none;
    }
  }
  .contributor__body {
    width: 100%;
  }

  .contributor__avatar,
  .contributor__body {
    display: table-cell;
    vertical-align: middle;
  }
    .contributor__name {
      @include gel-typography('brevier-bold');
    }
    .contributor__title {
      @include gel-typography('brevier-bold');
    }

    .contributor__name,
    .contributor__title {
      display: block;
    }
