.stripemenu{
  display: none;
}

.subheader{
  position: relative;
  &:after{
    content: '';
    display: block;
    position: absolute;
    border-right: 4px solid #03DCBC;
    height: 25px;
    right: 0;
    top: 8px;
  }
}
.stripenav {
  margin-top: 16px;
  overflow: hidden;
  ul{
    white-space: nowrap;
    overflow: scroll;
  }
}
.stripenav li {
    display: inline-block;
    height: 2.8rem;
    overflow: hidden;
}

.stripenav h2 {
    font-weight: 600;
    font-size: 20px;
    line-height: 43px;
    letter-spacing: 0.5px;
}

.stripenav li:first-child h2 a {
    padding-right: 24px;
    position: relative;
}

.stripenav li:nth-child(n+2) {
    margin-left: 24px;
}

.stripenav li:nth-child(n+2) {
    padding-top: .7rem;
}

.stripenav li>a {
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    letter-spacing: 0.5px;
    color: #000;
}
