///*------------------------------------*\
//    # iodine USED FOR TOP STORIES
//\*------------------------------------*/

.iodine__item{
  margin-bottom: $gel-spacing-unit * 2;
  position: relative;

  @include mq($from:gel-bp-m) {
    .promo{
      min-height: 222px;
    }
  }
}




 .iodine__item--0{
   .promo{
     min-height: inherit;
   }
   @include mq($from:gel-bp-m, $until:gel-bp-l) {
     @extend .promo--lead;
     .promo__media{
       float: left;
       padding-left: 0;
       padding-right: 4px;

     }
     .promo__body{
       padding-right: 0;
       padding-left: 12px;
     }
   }
   @include mq($from:gel-bp-l) {
   .promo__media{
     width: 100%;
     padding-left: 0;
   }
   .promo__body{
     width: 100%;
     padding-right: 0;
   }
  }
  @include mq($from:gel-bp-xxl) {
    .promo__media{
      width: 100%;
      padding-left: 0;
      margin-bottom: $gel-spacing-unit;
    }
  }
 }

 ///*------------------------------------*\
 //    # IODINE HERO PROMO FLOAT RIGHT
 //\*------------------------------------*/

.iodine--hero-right{
  .iodine__item--0{
     float: right;
   }
  .iodine__item{
    @include mq($from:gel-bp-l) {
      &:nth-last-child(-n+2){
        float: right;
      }

      &:nth-child(n+4):nth-child(-n+5){
        float: left;
      }
    }
  }
}

///*------------------------------------*\
//    # IODINE HERO PROMO FLOAT LEFT
//\*------------------------------------*/

.iodine--hero-left{
 .iodine__item--0{
    float: left;
  }
 .iodine__item{
   @include mq($from:gel-bp-l) {
     &:nth-last-child(-n+2){
       float: left;
     }

     &:nth-child(n+4):nth-child(-n+5){
       float: right;
     }
   }
 }
}
