///*------------------------------------*\
//    # oxygen USED FOR TOP STORIES
//    # HYDROGEN DEPENDENT
//\*------------------------------------*/


.maximum__item{
  padding-bottom: $gel-spacing-unit * 2;
    @include mq($from: 'gel-bp-xxl') {
      padding-bottom: $gel-alt-spacing-unit * 2;
    }
}

.maximum--box{width: 100%;}

.maximum__item {
  &:first-child{
    .promo__headline{
        @include gel-typography('trafalgar');
    }
  }
  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(12/1);
    &:first-child{
      .promo .metadata {
        position: inherit;
        bottom: inherit;
        }
      .promo__body{
        width: gel-columns(2/3);
        display: inline-block;
        padding-bottom: 8px;

      }
      .live_promo{
        width: gel-columns(1/3);
        float: right;
        margin-top: 16px;
        padding-left: 14px;
      }
      .see-also{
        width: gel-columns(2/3);
        float: left;
        display: inline-block;
        .see-also__item{
          vertical-align: text-top;
          width: gel-columns(1/2);
          display: inline-block;
          padding-right: $gel-spacing-unit * 2;
          &:last-child{
            padding-right: 0;
          }
        }
      }
    }
  }
  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
    float: left;
    width: gel-columns(1/4);
    &:first-child{
      width: gel-columns(12/1) !important;
      @include promo--lead (56.25%, $volume-setting:"maximum");
      .see-also{
        display: inline-block;
        width: gel-columns(3/4);
        float: left;

        .see-also__item{
          vertical-align: text-top;
          width: gel-columns(1/3);
          display: inline-block;
          padding-right: $gel-spacing-unit * 2;
          &:last-child{
            padding-right: 0;
          }
        }
      }
      .live_promo{
        width: gel-columns(1/4);
        float: right;
        margin-top: 16px;
        padding-left: 14px;
      }
    }
  }

  @include mq($from: 'gel-bp-xxl') {

    float: left;
    width: gel-columns(1/5);
    &:first-child{

      width: gel-columns(12/1) !important;
      @include promo--lead (56.25%, $volume-setting:"maximum");
      .live_promo{
        width: gel-columns(1/5);
        float: right;
        display: inline-block;
        margin-top: 16px;
        padding-left: 20px;
      }
      .see-also{
        width: gel-columns(1/5);
        display: inline-block;
      }
    }

  }
}
// HAS FOUR ITEMS
.maximum__item:nth-last-child(n+4):nth-last-child(-n+4):first-child, .maximum__item:nth-last-child(n+4):nth-last-child(-n+4):first-child ~ .maximum__item  {

  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(1/3);
    &:first-child{
      @include promo--lead (56.25%);
    }
  }

  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
    display: inline-block !important;
    float: left;
    width: gel-columns(2/4);
    &:first-child{
      width: gel-columns(2/4) !important;

    }
    &:nth-child(n+2):nth-child(-n+4){
      @include promo--horizontal (41.66667%);

    }
  }

  @include mq($from: 'gel-bp-xxl') {
    display: inline-block !important;
    float: left;
    width: gel-columns(2/5);
    &:first-child{
      width: gel-columns(3/5) !important;
      @include promo--lead;
    }
      @include promo--horizontal (33.33%);
  }
}

// HAS FIVE ITEMS
.maximum__item:nth-last-child(n+5), .maximum__item:nth-last-child(n+5) ~ .maximum__item {
  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(1/3  );
    &:first-child{
      width: gel-columns(2/3);
    }
  }
  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
      width: gel-columns(1/4);
      display: inline-block;
      float: left;
      .promo{
        min-height: 336px;
      }
    &:nth-child(n+4):nth-child(-n+5){
      display: inline-table;
      padding-bottom: $gel-spacing-unit * 2;
      .promo{
        min-height: 160px;
      }
      .promo__media{
        display: none;
      }

    }
  }
  @include mq($from: 'gel-bp-xxl') {
    width: gel-columns(1/5);
  }
}
