\*------------------------------------*/
/** The `Faux Block Link` pattern allows you to make an entire block a link, whilst having nested within in that block also clickable. The main link is absolutely positioned across the entire block, any nested links have a little z-index bump to lift them on top. Nice. <div class="mt-a-links-faux-block-link"> <div> <h1 class="at-links-faux-block-link__target"> <a href="{{url}}">{{title}}</a> </h1> <a href="{{a-second-url}}">A nested link</a> </div> <a class="at-links-faux-block-link__overlay" href="{{url}}" aria-hidden="true" tabIndex="-1"> Read more on {{title}} </a> </span> @link http://codepen.io/BPScott/pen/Erwan 1. IE fix to get hand cursor */
.at-links-faux-block-link { position: relative; }

.at-links-faux-block-link:hover { cursor: pointer; /* [1] */ }

