/*------------------------------------*\
    #LAYOUT
\*------------------------------------*/

    /**
     * We sometimes want to use flexbox to give us equal height columns
     * GEL Layout doesn't currently offer this so we'll add it here for now
     * when this gets moved to the GEL component we can remove it from here
     */
    .gel-layout--flex {
        @include flex-display;
        @include flex-wrap(wrap);

        .gel-layout__item {
            @include flex-display;
        }
    }

    @include mq($from:gel-bp-m, $until:gel-bp-l) {
      .layout--flex\@m{
          @include flex-display;
          @include flex-wrap(wrap);
           .gel-layout__item {
               @include flex-display;
           }
      }
    }

    @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
      .layout--flex\@l{
          @include flex-display;
          @include flex-wrap(wrap);
           .gel-layout__item {
               @include flex-display;
           }
      }
    }

    @include mq($from:gel-bp-xxl) {
      .layout--flex\@xxl{
          @include flex-display;
           .gel-layout__item {
               @include flex-display;
           }
      }
    }


    /*------------------------------------*\
        #GUTTERS
    \*------------------------------------*/

     @include mq($from:gel-bp-xxl){
       .gel-layout__item {
           padding-left: 24px;
       }


       .gel-layout {
         margin-left: -24px;
       }
     }

     /*------------------------------------*\
         #MARGINS
     \*------------------------------------*/

      @include mq($from:320px){
        .gel-wrap {
          padding-right: 16px;
          padding-left: 16px;
        }
      }
