///*------------------------------------*\
//    # MOST POPULAR
//\*------------------------------------*/
.mostpop__list {
    column-count: 2;
    padding-bottom: 46px;
}

.mostpop__list-item {
    position: relative;
    display: inline-block;
    width: 100%;
    padding-left: 16px;
}

.mostpop__list-item-rank {
    font-size: 48px;
    color: #bb1919;
    width: 44px;
    display: inline-table;
    float: left;
    letter-spacing: -6px;
    text-align: left;
    padding-right: 10px;
    line-height: 1.2;
    font-weight: 100;
}

.mostpop__list-item-headline {
    font-size: 16px;
    font-size: 1rem;
    line-height: 1.375;
    font-weight: bold;
    padding-top: 8px;
    position: absolute;
    color: #222222;
}

.mostpop__list-item:nth-child(10) .mostpop__list-item-rank {
    margin-left: -12px;
    padding-right: 12px;
    width: 52px;
}
@include mq($from: 'gel-bp-xs') {
  .mostpop__list {
    column-count: 1;
    padding-bottom: 26px;
  }
}

@include mq($from: 'gel-bp-m') {
  .mostpop__list {
    column-count: 2;
  }
  .mostpop__list-item-headline {
    font-size: 16px;
    line-height: 1.375;
  }
}
@include mq($from: 'gel-bp-l') {
  .mostpop__list-item {
      max-width: 465px;
    }
  .mostpop__list-item-headline {
      padding-bottom: 0px;
  }
}

@include mq($from: 'gel-bp-xxl') {
  .mostpop__list {
      column-count: auto;
      padding-bottom: 0px;
      column-gap: 20px;
  }
  .mostpop__list-item {
    width: gel-columns(1/5);
    padding-bottom: 30px;
  }
  .mostpop__list-item-headline {
    width: 194px;
    padding-bottom: 26px;
  }
}
