///*------------------------------------*\
//    # NITROGEN USED FOR TOP STORIES
//\*------------------------------------*/

.nitrogen__item{
  margin-bottom: $gel-spacing-unit * 2;

  @include mq($from:gel-bp-m) {
  .promo--standard {
      min-height: 314px;
  }
 }
  @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
    &:nth-last-child(-n+3) {
      float: left;
    }
  }
}




///*------------------------------------*\
//    # RIVER DISPLAYS HEADLINES ONLY
//\*------------------------------------*/

.nitrogen__river{
    .nitrogen__item--river{
      min-height: 86px;
      padding-top: $gel-alt-spacing-unit;
      padding-bottom: $gel-alt-spacing-unit;
     }



  .promo__media{
    display: none;
  }
  .promo__headline{
    @include gel-typography('english-bold');
  }
}
