@import 'normalize';
@import 'mixins';

// GEL
//

 // GEL Tools
 // - https://github.com/bbc/gel-sass-tools
 @import 'gel/gel-sass-tools/sass-tools';

// External Dependencies
// - https://github.com/sass-mq/sass-mq
 @import 'mq';

// // GEL Foundations
 // - https://github.com/bbc/gel-typography
 @import 'gel/gel-typography/typography';

 // - https://github.com/bbc/gel-grid/tree/v1
 @import 'gel/gel-grid/grid';

 @import 'gel/gel-responsive-image/gel-responsive-image';


// GEL Styling
// - Defaults

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4 {
  font-weight: normal;
}

a {
  text-decoration: none;
}
