/*------------------------------------*\
    # LIVE STATUS
\*------------------------------------*/

.gs-o-live-status__icon{
     color: $nw-c-red;
     svg {
        fill: $nw-c-red;
    }
}
.gs-o-live-status {
    display: inline-block;

        color: $nw-c-red;


    .#{$gs-svg-feature-class} & {
        display: none;
    }
}

.gs-o-live-status__icon {
  vertical-align: top;
  line-height: 1.5;
  width: 1em;
  height: 1em;
}

.gs-o-live-status__text {
    display: inline-block;
    vertical-align: baseline;
    text-transform: uppercase;
    font-weight: bold;
    color: $nw-c-red;

}

@-webkit-keyframes pulse {
    0% { opacity: 1; }
    50% { opacity: .3; }
    100% { opacity: 1; }
}

@keyframes pulse {
    0% { opacity: 1; }
    50% { opacity: .3; }
    100% { opacity: 1; }
}

.gs-o-live-status__icon #pulse {
  -webkit-animation: pulse 1.7s cubic-bezier(.25, .1, .25, 1) infinite;
          animation: pulse 1.7s cubic-bezier(.25, .1, .25, 1) infinite;
}
