///*------------------------------------*\
//    # oxygen USED FOR TOP STORIES
//    # HYDROGEN DEPENDENT
//\*------------------------------------*/


.iron__item{
  padding-bottom: $gel-spacing-unit * 2;
}

.iron--box{width: 100%;}

.iron__item:nth-last-child(n+3):nth-last-child(-n+3):first-child, .iron__item:nth-last-child(n+3):nth-last-child(-n+3):first-child ~ .iron__item  {

  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(1/3);
  }
  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
    width: gel-columns(1/4);
    &:first-child{
      width: gel-columns(2/4) !important;
    }
  }

  @include mq($from: 'gel-bp-xxl') {

    float: left;
    width: gel-columns(1/5);
    &:first-child{
      width: gel-columns(3/5) !important;
      @include promo--lead;
    }

  }
}
// HAS FOUR ITEMS
.iron__item:nth-last-child(n+4):nth-last-child(-n+4):first-child, .iron__item:nth-last-child(n+4):nth-last-child(-n+4):first-child ~ .iron__item  {

  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(1/3);
    &:first-child{
      @include promo--lead (56.25%);
    }
  }

  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
    display: inline-block !important;
    float: left;
    width: gel-columns(2/4);
    &:first-child{
      width: gel-columns(2/4) !important;

    }
    &:nth-child(n+2):nth-child(-n+4){
      @include promo--horizontal (41.66667%);

    }
  }

  @include mq($from: 'gel-bp-xxl') {
    display: inline-block !important;
    float: left;
    width: gel-columns(2/5);
    &:first-child{
      width: gel-columns(3/5) !important;
      @include promo--lead;
    }
      @include promo--horizontal (33.33%);
  }
}

// HAS FIVE ITEMS
.iron__item:nth-last-child(n+5), .iron__item:nth-last-child(n+5) ~ .iron__item {
  @include mq($from: 'gel-bp-m', $until:'gel-bp-l') {
    width: gel-columns(1/3  );
    &:first-child{
      width: gel-columns(2/3);
    }
  }
  @include mq($from: 'gel-bp-l', $until:'gel-bp-xxl') {
      width: gel-columns(1/4);
      display: inline-block;
      float: left;
      .promo{
        min-height: 260px;
      }
    &:nth-child(n+4):nth-child(-n+5){
      display: inline-table;
      padding-bottom: $gel-spacing-unit * 2;
      .promo{
        min-height: 122px;
      }
      .promo__media{
        display: none;
      }

    }
  }
  @include mq($from: 'gel-bp-xxl') {
    width: gel-columns(1/5);
    .promo{
      min-height: 240px;
    }
  }
}
