/*------------------------------------*\
    # KEY POINTS MODIFICATIONS \\DARIO//
\*------------------------------------*/

.live_promo{
  .metadata__time .icon--type{
    margin-right: 4px;
  }
}
p.live_promo__summary{
  @include gel-typography('long-primer');
  color: $summary-copy;
  margin-top: 4px;
}

.icon--keypoints {
  margin-top: auto;
  svg{
    fill: $postbox;
  font-size: 14px;
  }
}

.lx-keypoints-component-wrap {
    position:relative;
    border-top: 4px solid #cb0101;

     padding: 4px 8px 8px 8px;





    .lx-c-key-points{
      position:relative;
      &:before{
        content: none;
        display: block;
        position: relative;
        top: -$gel-alt-spacing-unit;
        border-top: 1px solid #ededed;
        left: -8px;
        width: calc(100% + #{double($gel-spacing-unit)})
      }
    }

}



/*------------------------------------*\
    # KEY POINTS
\*------------------------------------*/

.lx-c-key-points {
    list-style: none;
}

.keypoints--headline{
  .live-status-block-news{
    margin-right: 4px;
  }
}
.lx-c-key-points__item {
    position: relative;

    margin-bottom: $gel-alt-spacing-unit;

    &:before {
        position: absolute;
        top: 12px;
        left: 7px;
        bottom: -16px;

        width: 1px;

        background-color: $sp-c-silver;

        content: '';
    }


    &:last-child {
        &:before {
            display: none;
        }
    }
}

.lx-c-key-points__link {
    color: $sp-c-black;
    text-decoration: none;
}

.lx-c-key-points__icon {
    width: 19px;
    height: 18px;

}

.lx-c-key-points__timestamp {
    display: block;
    margin-bottom: halve($gel-spacing-unit);

    color: $sp-c-mortar;
}





.curium--slot {
  .lx-keypoints-component-wrap{
    max-height: 332px;
    overflow: hidden;
  }

}
