.metadata {
  @include nav;
  margin-top: 8px;
  margin-bottom: 0;

  li {
    // @include gel-typography('brevier');
    font-size: 13px;
    line-height: 16px;
    position: relative;
    margin-right: 6px;
    padding-right: 6px;
    &:after{
      content: "";
      display: block;
      position: absolute;
      border-right: 1px solid $keyline-color;
      top: 1px;
      right: -1px;
      height: 88%;
    }
    &:last-child {
      &:after{
      border-right: none;
      }
      margin-right: 0;
      padding-right: 0;
    }
  }
  a {}
  .icon {
    margin-right: 3px;
  }
}
  .metadata__time {
    color: $rhino;
    -webkit-font-smoothing: antialiased;
    vertical-align: bottom;
    .icon svg {
      fill: $rhino;
    }
  }
  .metadata__section {
    display: inline;
     a{
       color: $storm;
       @include mq($from:320px, $until:340px) {
        max-width: 98px;
       }
       @include mq($from:341px, $until:380px) {
        max-width: 124px;
       }
       @include mq($from:gel-bp-m, $until:760px) {
        max-width: 124px;
       }
       white-space: nowrap;
       overflow: hidden;
       text-overflow: ellipsis;
       vertical-align: middle;
    }
  }
  .metadata__comments {}
