.promo {
 width: 100%;

  .image {
    display: block;
  }

  .promo__summary {
    @include gel-typography('long-primer');
    color: $summary-copy;
    margin-top: $gel-spacing-unit;
 margin-bottom: 0;
  }

  .metadata{
    position: absolute;
      bottom: 0;
  }
}

.promo__headline {
  @include gel-typography('paragon-bold');
     margin-top: 0;
  margin-bottom: 0;
}


.promo__media {
  margin-bottom: $gel-spacing-unit;
    position: relative;
    // Label's inside thumbs get positioned absolutely
    .promo__label {
      position: absolute;
      top: 0; left: 0;
      z-index: 10;
      box-shadow: 0 1px 6px rgba(0,0,0,.15);
    }
  }
    .promo__label {
     margin-bottom: $gel-spacing-unit;
    }
  .promo__body {
    padding-bottom: $gel-alt-spacing-unit * 2;
  }




///*------------------------------------*\
//    # LEAD PROMO
//\*------------------------------------*/

@mixin promo--lead($image-depth:63%, $volume-setting:"standard") {
  width: 100%;



  .metadata{
    position: initial;
  }

  .promo__body{
    padding-bottom: 0;
  }
  .promo__headline{
   @include gel-typography('agate-bold');
   }

  @include mq($from:gel-bp-xs, $until:720px) {
   .promo__headline{
      width: gel-columns(12/1) !important;
      display: block !important;
      margin-bottom: $gel-spacing-unit;
    }
    .promo__summary{
      margin-top: 0 !important;
    }
  }


  @include mq($from:gel-bp-m) {

    .promo__headline{
       display: inline-block;
    }

    @if $volume-setting == splash{


    } @else if $volume-setting == maximum {
       .promo__media {
         @include mq($from:gel-bp-l) {
           width: gel-columns(3/4);
            padding-right: $gel-spacing-unit / 2;
            margin-bottom: 0;
             float: left;
         }
           @include mq($from:gel-bp-xxl) {
         width: gel-columns(3/5);
          padding-right: $gel-spacing-unit;
       }


       }
       .promo__body {
         .promo__headline{
             @include gel-typography('trafalgar');
         }
         @include mq($from:gel-bp-m) {
             width: gel-columns(2/3);
         }
          @include mq($from:gel-bp-l) {
            width: gel-columns(1/4);
            padding-left: $gel-alt-spacing-unit;
        }
          @include mq($from:gel-bp-xxl) {
            width: gel-columns(2/5);
            padding-left: $gel-spacing-unit * 2;

          }
       }
       .lx-keypoints-component-wrap{
         @include mq($from:gel-bp-l) {

         }
       }
       .see-also {
           @include mq($from:gel-bp-xxl) {
            padding-left: $gel-alt-spacing-unit;
           }
           @include mq($from:gel-bp-xxl) {
            padding-left: $gel-spacing-unit * 2;
           }
       }

     }@else {
       .promo__media {
         width: gel-columns(2/3);
         padding-left: halve($gel-spacing-unit);
         float: right;
         order: 1;
         margin-bottom: 0;
       }
       .promo__body {
         width: gel-columns(1/3);
       }
    }


    .promo__body {
      float: left;
      padding-right: $gel-alt-spacing-unit;
    }
  }
    @include mq($from:gel-bp-l) {
    .gel-responsive-image--16by9{
            padding-bottom: $image-depth;
      }
      .promo__summary{
        margin-top: 16px;
      }
    }
  @include mq($from:gel-bp-xxl) {
    @include flex-direction(row);

  }
}

.promo--lead{
    @include promo--lead;
}

@include mq($from:gel-bp-xxl) {
  .promo--lead\@xxl{
    @include promo--lead;
  }
}


///*------------------------------------*\
//    # PROMO HORIZONTAL
//\*------------------------------------*/

@mixin promo--horizontal($image-width:gel-columns(10/24)){

   .promo__media {
      display: none;
      position: relative;
      display: block;
      width: $image-width;
      float: left;
      margin-bottom: 0;
       }

   &.promo--has-media .promo__body {
      width: 58.3333333333%;
      padding-left: $gel-spacing-unit;
    }

   .promo__body{
      overflow: hidden;
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

   }
   .promo__media + .promo__body{
     padding-left: $gel-spacing-unit;
   }
   @include mq($from:gel-bp-xs, $until:gel-bp-m) {
    .promo__summary {
       display: none;
      }
  }
}


@include mq($from:gel-bp-xs, $until:gel-bp-m) {
  .promo--horizontal\@xs{
    @include promo--horizontal;
  }
}

@include mq($from:gel-bp-l, $until:gel-bp-xxl) {
  .promo--horizontal\@l{
    @include promo--horizontal;
  }
}

///*------------------------------------*\
//    # PROMO TEXT OVER IMAGE
//\*------------------------------------*/
@mixin promo--text-over-image{
  .promo__media{
    margin-bottom: 0;

    .media-type__duration{
      display: none;
    }

    &:before {
    content: "";
    display: block;
    position: absolute;
    background-image: -webkit-linear-gradient(top, rgba(0,0,0,0) 0, rgba(0,0,0,0) 40%, #000 95%);
    width: 100%;
    z-index: 1;
    bottom: 0;
    height: 100%;
    }
  }
  .promo__body{
    z-index: 2;
    position: absolute;
    bottom: 8px;
    a{
      color: #fff;
    }
    .metadata__time .icon svg, .metadata__comments .icon svg{
      fill: #ededed;
    }
    .metadata li{
      color: #ededed;
    }
  }
  .promo__summary{
    display: none;
  }
  &.icon--large {
    .media-type{
      padding: 8px 8px 7px 8px;
      margin-left: 12px;
      margin-bottom: 38px;
      z-index: 1;
    }
    .promo__body{
      padding-left: 78px;
      padding-right: 16px;
    }
  }
}

@include mq($from:gel-bp-l, $until:gel-bp-xxl) {
  .promo--text-over-image\@l{
    @include promo--text-over-image;
  }
}

///*------------------------------------*\
//    # PROMO CARD
//\*------------------------------------*/

.promo--card{
  background-color: #fff;

  .promo__media{
    margin-bottom: 0;
  }
  .promo__body {
    @include mq($from:gel-bp-m){
    padding-bottom: 40px;
    }
    padding-bottom: $gel-spacing-unit * 2;
    padding-top: $gel-alt-spacing-unit ;
    padding-left: $gel-spacing-unit * 2;
    padding-right: $gel-spacing-unit * 2;
  }

  .media-type{
    background-color: #fff !important;
  }
  .see-also{
    padding-bottom: $gel-spacing-unit;
    padding-top: 0;
    padding-left: $gel-spacing-unit * 2;
    padding-right: $gel-spacing-unit * 2;
  }
  .metadata{
    bottom: 8px;
  }
}
