.see-also {
  @include nav;
  @include nav--stacked;
  margin-top: 8px;
  margin-bottom: 0;

  li {

    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    @include gel-typography('long-primer-bold');
    padding-top: 4px;
    padding-bottom: 4px;
      @include mq($from: 'gel-bp-m') {
          padding-top: $gel-spacing-unit;
          padding-bottom: 0px;
      }
  }
  .label--icon {
    margin-right: 4px;
  }

  .see-also--bottom &{
    width: 100%;
    display: inline-block;

    .see-also__item{
      @include mq($from: 'gel-bp-m') {
      vertical-align: text-top;
      width:  gel-columns(4/12);
      display: inline-block;

        + .see-also__item {
          padding-left: 8px;
        }
      }
        @include mq($from: 'gel-bp-xxl') {
        + .see-also__item {
          padding-left: 12px;
        }
      }
    }
  }
}

.see-also--bottom .gel-responsive-image--16by9{
  padding-bottom: 56.25% !important;
}
