/*------------------------------------*\
    # KEYLINE
\*------------------------------------*/

@mixin keyline-top (){ position: relative; padding-top: $gel-spacing-unit * 2 ;&:before { position: absolute;right: 0;top: 0px;left: 16px;display: block;border: 0 solid $keyline-color;border-top-width: 1px !important;content: '';}}
@mixin keyline-right (){ position: relative; &:before { position: absolute;right: -8px;top: 0;bottom: 0;left: auto;display: block;border: 0 solid $keyline-color;border-right-width: 1px !important;content: '';}}
@mixin keyline-bottom (){ position: relative;  &:after { position: absolute;right: 0;top: auto;bottom: 0px;left: 24px;display: block;border: 0 solid $keyline-color;border-bottom-width: 1px !important;content: '';}}
@mixin keyline-left (){ position: relative;  &:after { position: absolute;right: auto;top: -8px;bottom:0;left: 8px;display: block;border: 0 solid $keyline-color;border-left-width: 1px !important;content: '';}}




// Which breakpoints do we want spacing classes generated for?
//
// @type List
//
$at-spacing-breakpoints: (s, m, l, xxl) !default;


// Output the spacing classes, adding a breakpoint namespace if required
//
// @param {String}  $breakpoint - breakpoint namespace you want to apply
//
// @access          private
//
// @author          Shaun Bent
//
@mixin _output-spacing-classes($breakpoint: null) {
    $breakpoint-suffix: '';

    @if $breakpoint != null {
        $breakpoint-suffix: \@#{$breakpoint};
    }


    /**
     * Margin spacing classes
     */
    .at-line-t#{$breakpoint-suffix}    { @include keyline-top;  }
    .at-line-r#{$breakpoint-suffix}    { @include keyline-right; }
    .at-line-b#{$breakpoint-suffix}    { @include keyline-bottom; }
    .at-line-l#{$breakpoint-suffix}    { @include keyline-left; }


    @include mq($from:gel-bp-xs, $until:gel-bp-m) {
      .at-line-t-until\@s{
        @include keyline-top;
        padding-top: 12px;
        &:before{
          left: 8px;
        }
      }
      .at-line-r-until\@s{
        @include keyline-right;
      }
      .at-line-b-until\@s{
        @include keyline-bottom;
      }
      .at-line-l-until\@s{
        @include keyline-left;
      }
    }
    @include mq($from:gel-bp-m, $until:gel-bp-l) {
      .at-line-t-until\@m{
        @include keyline-top;
      }
      .at-line-r-until\@m{
        @include keyline-right;
      }
      .at-line-b-until\@m{
        @include keyline-bottom;
      }
      .at-line-l-until\@m{
        @include keyline-left;
      }
    }
    @include mq($from:gel-bp-l, $until:gel-bp-xxl) {
      .at-line-t-until\@l{
        @include keyline-top;
      }
      .at-line-r-until\@l{
        @include keyline-right;
      }
      .at-line-b-until\@l{
        @include keyline-bottom;
      }
      .at-line-l-until\@l{
        @include keyline-left;
      }
    }

}

/**
 * Output the spacing classes and loop through the array of
 * breakpoints output the breakpoint namespaced classes
 */
@include _output-spacing-classes();

@each $breakpoint in $at-spacing-breakpoints {
    @include mq($from: '#{$gel-grid-breakpoint-namespace}#{$breakpoint}') {
        @include _output-spacing-classes($breakpoint);
    }
}
