/*------------------------------------*\
    #RESPONSIVE IMAGE
\*------------------------------------*/

/**
 * The responsive image pattern automatically reserves the space
 * needed for an image to be inserted
 */


// We need to check that the GEL settings are available globally
$gel-settings-available: false !default;
@if ($gel-settings-available == false) {
    @warn "Missing Dependency: Have you included the GEL settings?";
}



// define some output control variables
$core: true !default;
$enhanced: true !default;


// Varabiles to allow customisation and condifuration of the component
$gel-responsive-image-namespace: $gel-namespace !default;

$gel-responsive-image-bg: #f0f0f0 !default;

$gel-responsive-image-enable--watermark: false !default;



@if $core {

    /**
     * 1. Setting the height to zero collapses the div so we don't
     *    need to absolutly position the image
     * 2. Stop images 1px difference between the reserved space
     *    and the actual image from showing
     */
    .#{$gel-responsive-image-namespace}responsive-image,
    %#{$gel-responsive-image-namespace}responsive-image {
        position: relative;

        height: 0; /* [1] */
        overflow: hidden; /* [2] */

        background-color: $gel-responsive-image-bg;

        @if $gel-responsive-image-enable--watermark {
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 30%;
        }

        /**
         * 1. Force the image to sit on top of the placeholder
         * 2. Force the image to fill the available space
         * 
         * The exact combination of [1] and [2] is needed to stop the
         * image from being 1px too short, the root cause of which is
         * down to the browser's rendering of sub-pixels         
         */
        img {
            position: absolute; /* [1] */
            width: 100%; /* [2] */
            height: 100%; /* [2] */
        }
    }

    /**
     * Aspect radio modifiers
     */
    .#{$gel-responsive-image-namespace}responsive-image--1by1,
    %#{$gel-responsive-image-namespace}responsive-image--1by1 {
        padding-bottom: 100%;
    }

    .#{$gel-responsive-image-namespace}responsive-image--16by9,
    %#{$gel-responsive-image-namespace}responsive-image--16by9 {
        padding-bottom: 9 / 16 * 100%;
    }
}