.label {
  @include gel-typography('brevier');
  background-color: #000;
  color: #FFF;
  display: inline-block;
  line-height: 1;
  padding: 4px 8px;
  vertical-align: middle;
  font-weight: bold;
  text-transform: uppercase;

  // -- Label Colour
  .label--newsbeat & {
    background-color: $newsbeat;
  }

  .label--breaking &{
    background-color: $news;
  }


  // -- Label Structural
  .label--inline &{
    margin-top: -0.25em;
  }


}
